/* 움직이는 글자 만들기 */
.bounce {
    display: inline-block;
    animation: bounce 1s ease-in-out 1;
    animation-iteration-count: 1; 
}
  
@keyframes bounce {
    0%, 100% {
        color: #FFFFFF;
        transform: scaleX(1) translateY(0);
    }
    30% {
        transform: scaleX(1.3) translateY(20%);
    }
    55% {
        color: #ff00d6;
        transform: scaleX(0.6) translateY(-10%);
    }
    70% {
        color: #fc83e8;
        transform: scaleX(1) translateY(0);
    }
    85% {
        transform: scaleX(0.8) translateY(-10%);
    }
}
  
